.projects {
  margin: 20px 0;
  padding: 15px;
  background: #F2F2F2;
}

.projects__item-content {
  img {
    width: 100%;
  }
}

.projects__item-content-inner {
  padding: 25px 20px;
  a {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;

    color: #000000;
  }
}

.projects__item-content-info {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: #000000;
  margin-top: 25px;
  display: flex;
  flex-wrap: wrap;
  border-top: 1px solid #E9E9E9;
  margin-left: -20px;
  margin-right: -20px;
  padding: 25px 20px 0 20px;

  p {
    margin: 0;
    margin-right: 10px;
  }
}

.projects__item {
  background: white;
  position: relative;
  &:hover {
    .projects__item-hover {
      opacity: 1;
      transition: all 0.3s;
    }
  }
  &:not(:last-child) {
    margin-bottom: 15px;
  }
}

.projects__item-hover {
  position: absolute;
  width: 100%;
  height: 100% ;
  left: 0;
  top: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  opacity: 0;
  transition: all 0.3s;
  flex-direction: column;
  justify-content: flex-end;
  .projects__item-content-inner {
    position: relative;
    z-index: 10;
      a {
        color: white;
      }

    .projects__item-content-info {
      border-top: none;
      margin: 20px 0;
      padding: 0;
      color: white;
    }

    .main__btn {
      font-size: 12px;
      line-height: 15px;
    }
  }
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(57, 57, 57, 0.72);
    position: absolute;
    left: 0;
    top: 0;
  }

}

.projects__item-content-more {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  .icon {
    background-position: center;
    background-repeat: no-repeat;
    content: '';
    display: block;
    width: 50px;
    height: 50px;
  }
}


.projects__icons {
  position: absolute;
  left: 15px;
  top: 15px;
  display: flex;
  flex-direction: row-reverse;
}

.projects__icon {
  content: '';
  display: block;
  position: relative;
  width: 45px;
  height: 45px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #FFFFFF;
  border-radius: 50%;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  transition: all 0.3s;
  &:hover {
    background-color: #FFC43E;
    transition: all 0.3s;
    z-index: 10;

    .projects__icon-info {
      opacity: 1;
      transition: all 0.3s;
    }
  }
  &:not(:last-child) {
    margin-left: -10px;
  }

}

.projects__icon-info {
  position: absolute;
  font-size: 14px;
  line-height: 16px;
  text-align: center;

  color: #282828;
  background: #FFC43E;
  border-radius: 0px 16px 16px 16px;
  width: auto;
  padding: 10px 15px;
  white-space: nowrap;
  bottom: calc(-100% - 15px);
  left: 50%;
  opacity: 0;
  transition: all 0.3s;

  &::before {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-bottom: 15px solid #FFC43E;
    border-right: 15px solid transparent;
    left: 0;
    top: -10px;

  }

}
