  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  padding: 0;
  color: black;
  font-size: 16px; }

button {
  cursor: pointer; }

a {
  text-decoration: none;
  color: black; }

* {
  box-sizing: border-box; }

.main__btn {
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */
  color: #FFFFFF;
  border: none;
  background: #C8A481;
  border-radius: 30px;
  padding: 17.5px 20px;
  text-transform: uppercase; }

.main__title-wrapper {
  position: relative;
  padding: 0 15px;
  overflow-x: hidden;
  min-height: 120px;
  display: flex;
  align-items: center; }

.main__title {
  font-family: 'Oswald', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
