  text-transform: uppercase;
  color: #2C2C2C;
  position: relative;
  z-index: 20; }
  .main__title span {
    color: #C29F7E; }

.main__title-shadow {
  font-weight: 600;
  font-size: 120px;
  text-transform: lowercase;
  line-height: 120px;
  color: #F7F7F7;
  position: absolute;
  top: -25px;
  left: 0; }

.main__desc {
  padding: 0 15px;
  font-size: 16px;
  line-height: 23px;
